//todoitem.js is a child of todo.js and inherits its properties and its parents  
import React from 'react'
// Destructuring: can write props.todo or {todo} compo of props are destructured
// and written separately
export const TodoItem = ({todo,onDelete}) => {
  return (
    <>
    <div>
      <h4>{todo.Title}</h4>
      <p>{todo.Desc}</p>
      <button className="btn btn-sm btn-danger" onClick={()=>{onDelete(todo)}}>Delete</button>
      {/* btn btn-sm btn is a class of button it decreases the size */}
    </div>
    <hr/>
    </>
  )
}
