import './App.css';
import Header from './MyComponents/Header';
import { Todos } from './MyComponents/Todos';
import { Footer } from './MyComponents/Footer';
import React, { useState, useEffect } from 'react';
import { AddTodo } from './MyComponents/AddTodo';
import { About } from './MyComponents/About';
import {
  // for linking differnt pages
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {
  let initTodo;
  if (localStorage.getItem("todos") === null) {
    initTodo = [];
  }
  else {
    initTodo = JSON.parse(localStorage.getItem("todos"));
  }
  // arrow func allows to write shorter functions
  const onDelete = (todo) => {
    setTodos(todos.filter((e) => {
      return e !== todo;
    }));
    localStorage.getItem("todos", JSON.stringify(todos));

  }
  const addTodo = (Title, Desc) => {
    let sno;
    if (todos.length === 0) {
      sno = 0;
    }
    else {
      sno = [todos.length - 1].sno + 1;
    }
    const myTodo = {
      sno: sno,
      Title: Title,
      Desc: Desc,
    }
    setTodos([...todos, myTodo]);
    console.log(myTodo);


  }
  const [todos, setTodos] = useState(initTodo);
    useEffect(() => {
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos])
  return (

    // classNamename is not in html its an jsx extension
    <>
    <Router>
    
      <Header title="My Todos List" searchBar={false} />
      {/* app.js is the parent and which has child todos app.js has 2 elements
    todos and ondelete which todos.js */}
      
      {/* switch chooses each option one by one */}
      <Switch>
            <Route exact path="/" render={()=>{
              return (
              <>
              <AddTodo exact addTodo={addTodo} />
              <Todos todos={todos} onDelete={onDelete} />
              </>)
            }}>
            </Route>
      
            <Route exact path="/about">
              <About/>
            </Route>
        
        </Switch>
      <Footer />
    </Router>
    </>
  );
}

export default App;
