import React from 'react'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
// props is used to pass info from parent to child
export default function Header(props) {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
    <div className="container-fluid">
        <Link className="navbar-brand" to="/">{props.title}</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
            <Link className="nav-link active" aria-current="page" to="/">Home</Link>
            </li>
            <li className="nav-item">
            <Link className="nav-link" to="/about">About</Link>
            </li> 
        </ul>
        {/* ternary variables {props.searchbar? <<condition>> true/false */}
        {props.searchBar? <form className="d-flex">
            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
            <button className="btn btn-outline-success" type="submit">Search</button>
        </form>:""}
        </div>
    </div>
    </nav>
  )
}
// gives default value if not given 
// its useful if you want to use same syntax in other web
// sites aswell
Header.defaultProps={
  title:" Your Title Here"
}
Header.propTypes={
  title: PropTypes.string,
  // shows an error in inspect when int is used
  searchBar: PropTypes.bool.isRequired
}